import React from "react";

export default function Work(props) {
  return (
    <li
      className="work_item"
      style={{
        backgroundImage: `url(https://github.com/BatistaTony/batistaoliveira/blob/gh-pages/img/${props.img}?raw=true`
      }}
    >
      <div>
        <h1>{props.nome}</h1>
        <a href={props.link} target="_blank" rel="noopener noreferrer">
          <img src="img/icons8_github_48px.png" alt="github" />
        </a>
      </div>
    </li>
  );
}
