import React, { useState } from "react";
import "./styles/servicos.scss";

export default function Servicos(props) {
  const [services, setServices] = useState([
    {
      title: "Desenvolvimento de Aplicações web",
      img: "img/icons8_web_design_100px.png",
      descricao:
        "Desenvolvimento de aplicações web para empresa, criação de websites responsivo e profissional para publicidades de productos ou outros fins."
    },
    {
      title: "Desenvolvimento de Aplicações Desktop",
      img: "img/icons8_trello_100px.png",
      descricao:
        "Desenvolvimento de aplicação para gestão de vendas d productos para qualquer tipo de empresa e sector."
    } 
  ]);

  return (
    <div className="servicos">
      <ul className="list_serv">
        {services.map(s => (
          <li>
            <img src={s.img} alt="" />
            <h1>{s.title}</h1>
            <p>{s.descricao}</p>
          </li>
        ))}
      </ul>
    </div>
  );
}
